*{
  box-sizing: border-box;
}
body{
  background-color: #f7f7f7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
}
h3{
  text-align: center;
}
.container {
  margin: 1em auto;
  width: 22.5rem;
  max-width: 100%;
}
.inc-exp-container {
  background-color: #fff;
 padding: 1em;
 display: flex;
 justify-content: space-between;
 
}

.inc-exp-container > div {
  flex: 1;
  text-align: center;
}

.inc-exp-container > div:first-of-type {
  border-right: 1px solid #dedede;
}

.money {
  font-size: 1.25rem;
  letter-spacing: 1px;
  margin: 5px 0;
}
.money.plus {
  color: #2ecc71;


}

.money.minus {
  color: #c0392b;
}

label {
 display: block;
  margin: 0.75em 0;
}

input[type='text'],
input[type='number']{
  border: 1px solid #dedede;
  border-radius: 2px;
  font-size: 1rem;
  padding: 0.5em;
  width: 100%;
}

.btn{
  cursor: pointer;
  background-color: blueviolet;
  color: white;
  border: 0;
  font-size: 1rem;
  margin: 0.75em 0 1.25em;
  padding: 0.75em;
  width: 100%;
}
.btn:focus, .delete-btn:focus{
  outline: 0;
}
.list{
  list-style-type: none;
  margin-bottom: 3em;
  padding: 0;
}
.list li{
  background-color: white;
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 0.75em;
  margin: 0.75em 0;
  
}

.list li.plus {
  border-right: 5px solid #2ecc71;
}

.list li.minus {
  border-right: 5px solid #c0392b;
}

.delete-btn {
  cursor: pointer;
  background-color: #e74c3c;
  border: 0;
  border-radius: 2px;
  color: white;
  font-size: 1.25em;
  line-height: 1.25em;
  padding: 2px 5px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease;
  
}

.list li:hover .delete-btn {
  opacity: 1;
}

@media screen and (min-width:320px) and (max-width: 480px){
  .container {
   
    width: 17.5rem;
    max-width: 100%;
  }
}

